@import "https://fonts.googleapis.com/css?family=Roboto+Mono&subset=greek,cyrillic";
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: Roboto Mono, monospace;
  text-decoration: none;
}

:root {
  --bg-color: #1f242d;
  --second-bg-color: #0c1a1a;
  --text-color: #fff;
  --main-color: #2272ff;
}

html {
  font-size: 62.5%;
  overflow: scroll;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  overflow-y: auto;
}

section {
  min-height: 100vh;
  padding: 10rem;
}

.header {
  width: 100%;
  z-index: 100;
  background-color: #000b38;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.header.sticky {
  border-bottom: .1rem solid #0003;
}

.logo {
  color: var(--text-color);
  cursor: pointer;
  font-size: 2.5rem;
  font-weight: 600;
}

.navbar a {
  color: var(--text-color);
  margin-left: 4rem;
  font-size: 1.7rem;
  transition: all .3s;
}

.navbar a:hover, .navbar a.active {
  color: var(--main-color);
}

#menu-icon {
  color: var(--text-color);
  font-size: 3.6rem;
  display: none;
}

.home {
  justify-content: center;
  align-items: center;
  display: flex;
}

.home-content h3 {
  font-size: 3.2rem;
  font-weight: 700;
}

.home-content h3:nth-of-type(2) {
  margin-bottom: 2rem;
}

.btn-hire {
  box-shadow: 0 0 1rem var(--main-color);
  color: var(--second-bg-color);
  letter-spacing: .1rem;
  background-color: azure;
  border-radius: 4rem;
  padding: 1rem 2.8rem;
  font-size: 1.6rem;
  font-weight: 600;
  display: inline-block;
}

.btn-hire:hover {
  background-color: #6a5acd;
}

span {
  color: var(--main-color);
}

.home-content h1 {
  font-size: 5.6rem;
  font-weight: 700;
  line-height: 1.3;
}

.home-content p {
  font-family: Verdana, Arial, Helvetica, sans-serif;
  font-size: 2.1rem;
}

.home-img img {
  width: 30vw;
  margin-right: 4%;
  animation: 4s ease-in-out infinite floatImage;
}

@keyframes floatImage {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-2.4rem);
  }

  100% {
    transform: translateY(0);
  }
}

.social-media a {
  width: 4rem;
  height: 4rem;
  border: .2rem solid var(--main-color);
  color: var(--main-color);
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 3rem 1.5rem 3rem 0;
  font-size: 2rem;
  transition: all .4s;
  display: inline-flex;
}

.social-media a:hover {
  background: var(--main-color);
  color: var(--second-bg-color);
  box-shadow: 0 0 1 rem var(--main-color);
}

.btn {
  box-shadow: 0 0 1rem var(--main-color);
  color: var(--second-bg-color);
  letter-spacing: .1rem;
  background-color: azure;
  border-radius: 4rem;
  padding: 1rem 2.8rem;
  font-size: 1.6rem;
  font-weight: 600;
  display: inline-block;
}

.btn:hover {
  box-shadow: none;
}

.about {
  background-color: #201f23;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.about-image img {
  width: 29vw;
}

.heading {
  text-align: center;
  font-size: 4.5rem;
}

.about-content h2 {
  text-align: left;
  line-height: 1.2;
}

.about-content h3 {
  font-size: 2.6rem;
}

.about-content p {
  margin: 2rem 0 3rem;
  font-size: 1.6rem;
}

.services h2 {
  margin-bottom: 5rem;
}

.services-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  display: flex;
}

.services-container .services-box {
  background: var(--second-bg-color);
  text-align: center;
  border: .2rem solid var(--bg-color);
  filter: blur(1px);
  border-radius: 2rem;
  flex: 30rem;
  padding: 3rem 2rem 4rem;
  transition: all .5s;
}

.services-container .services-box:hover {
  border-color: var(--main-color);
  transform: scale(1.02);
}

.services-box:hover {
  opacity: 100;
  filter: blur();
  filter: saturate(20);
  filter: brightness(4);
}

.services-box i {
  color: var(--main-color);
  font-size: 7rem;
}

.services-box h3 {
  font-size: 2.6rem;
}

.services-box p {
  margin: 1rem 0 3rem;
  font-size: 1.6rem;
}

.portfolio {
  background: #4b4b4b;
}

.portfolio h2 {
  color: #e2dfd2;
  margin-bottom: 4rem;
}

.portfolio-container {
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  gap: 2.5rem;
  display: grid;
}

.portfolio-container .portfolio-box {
  box-shadow: 0 0 1rem var(--bg-color);
  border-radius: 2rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.portfolio-box img {
  width: 100%;
  transition: all .5s;
}

.portfolio-box:hover img {
  transform: scale(1.1);
}

.portfolio-box .portfolio-layer {
  width: 100%;
  height: 100%;
  text-align: center;
  background: linear-gradient(#0000001a, #060650);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 4rem;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.portfolio-layer h4 {
  color: #fff;
  font-size: 2rem;
}

.portfolio-layer p {
  margin: .3rem 0 1rem;
  font-size: 1.6rem;
}

.portfolio-layer a {
  width: 5rem;
  height: 5rem;
  background: var(--text-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.portfolio-layer a i {
  color: var(--second-bg-color);
  font-size: 2rem;
}

.portfolio-box:hover .portfolio-layer {
  transform: translateY(0);
}

.contact h2 {
  margin-bottom: 3rem;
}

.contact form {
  max-width: 70rem;
  text-align: center;
  margin: 1rem auto 3rem;
}

.contact form .input-box {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.contact form .input-box input, .contact form textarea {
  width: 100%;
  color: var(--text-color);
  background-color: #070800;
  border-radius: .8rem;
  margin: .7rem 0;
  padding: 1.5rem;
  font-size: 1.6rem;
}

.contact form .input-box input {
  width: 49%;
}

.contact form textarea {
  resize: none;
}

.contact form .btn {
  cursor: pointer;
  margin-top: 2rem;
}

@media (width <= 1150px) {
  html {
    font-size: 55%;
  }

  .footer {
    width: 60%;
  }
}

@media (width <= 1200px) {
  html {
    font-size: 55%;
  }

  .footer {
    width: 70%;
  }
}

@media (width <= 991px) {
  .header {
    padding: 2rem 3%;
  }

  .services {
    padding-bottom: 7rem;
  }

  section {
    padding: 10rem 3% 2rem;
  }

  .footer {
    width: 100%;
    padding: 2rem 3%;
  }
}

@media (width <= 858px) {
  #menu-icon {
    display: block;
  }

  .navbar {
    width: 100%;
    background: var(--bg-color);
    border-top: .1rem solid #0003;
    padding: 1rem 3%;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    box-shadow: .5rem 1rem #0003;
  }

  .navbar.active {
    display: block;
  }

  .navbar a {
    margin: 3rem 0;
    font-size: 2rem;
    display: block;
  }

  .home {
    flex-direction: column;
  }

  .home-content h3 {
    font-size: 2.6rem;
  }

  .home-content h1 {
    font-size: 5rem;
  }

  .home-img img {
    width: 70vw;
    margin-top: 4rem;
  }

  .about {
    flex-direction: column-reverse;
  }

  .about img {
    width: 70vw;
    margin-top: 4rem;
  }

  .services h2 {
    margin-bottom: 3rem;
  }

  .portfolio h2 {
    padding-bottom: 3rem;
  }

  .footer {
    padding: 2rem 3%;
  }
}

@media (width <= 450px) {
  html {
    font-size: 50%;
  }

  .contact form .input-box input {
    width: 100%;
  }
}

@media (width <= 365px) {
  .home-img img {
    width: 90vw;
  }

  .footer {
    text-align: center;
    margin-top: 2rem;
  }
}

